import React, { Component } from "react";
import Chart from "chart.js";
import ChartDataLabels from "chartjs-plugin-datalabels";
import ChartComponent from "react-chartjs-2";
import getChartDataFromData from "./utils/getChartDataFromData";

function getPadding (chartType) {
  if (chartType === 'horizontalBar') {
    return { right: 50 }
  } else {
    return {}
  }
}

function getChartOptions({ chartTitleText, chartType }) {
  const chartTitle = chartTitleText
    ? {
        display: true,
        text: chartTitleText,
        position: "bottom"
      }
    : null;
  const scales = chartType !== 'radar'
    ? {
        xAxes: [
          {
            ticks: {
              autoSkip: false
            },
          }
        ],
        yAxes: [
          {
            ticks: {
              beginAtZero: true,
              autoSkip: false
            }
          }
        ]
      }
    : null;
  const padding = getPadding(chartType);
  return {
    title: chartTitle,
    elements: {
      line: {
        tension: 0
      }
    },
    layout: {
      padding,
    },
    scales,
    plugins: {
      datalabels: {
        align: "end",
        anchor: "end"
      }
    },
  };
}

class ChartDisplay extends Component {
  componentDidMount() {
    Chart.pluginService.register(ChartDataLabels);
  }

  render() {
    const { redraw, data, selectedChartType, chartTitleText } = this.props;
    return (
      <ChartComponent
        id="chart"
        redraw={redraw}
        data={getChartDataFromData(data, selectedChartType)}
        type={selectedChartType}
        options={getChartOptions({
          chartTitleText,
          chartType: selectedChartType
        })}
      />
    );
  }
}

export default ChartDisplay;
