import React, { Component } from "react";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import Link from "@material-ui/core/Link";
import { MuiThemeProvider } from "@material-ui/core/styles";
import ReactDataSheet from "react-datasheet";
import "react-datasheet/lib/react-datasheet.css";
import "./App.css";
import getReactDataSheetDataFromData from "./utils/getReactDataSheetDataFromData";
import { CHART_TYPES, DEFAULT_DATA, MUI_THEME } from "./utils/constants";
import ChartTypeSelector from "./ChartTypeSelector";
import ChartDisplay from './ChartDisplay';

function downloadImage(filename) {
  const link = document.createElement("a");
  link.download = `${filename}.png`;
  link.href = document.getElementById("chart").toDataURL();
  link.click();
}

function DownloadButton({ chartTitleText }) {
  return (
    <div className="download-btn-wrapper">
      <Button
        variant="contained"
        color="primary"
        className="download-btn"
        onClick={() => downloadImage(chartTitleText)}
      >
        DOWNLOAD IMAGE
      </Button>
    </div>
  );
}

class App extends Component {
  state = {
    selectedChartType: CHART_TYPES[0],
    data: DEFAULT_DATA,
    redraw: false
  };

  render() {
    const chartTitleText = this.state.data[0][0];
    return (
      <div id="App">
        <Grid container spacing={24}>
          <MuiThemeProvider theme={MUI_THEME}>
            <Grid item xs={12} md={6}>
              <ChartDisplay
                redraw={this.state.redraw}
                data={this.state.data}
                selectedChartType={this.state.selectedChartType}
                chartTitleText={chartTitleText}
              />
              <ChartTypeSelector
                chartType={this.state.selectedChartType}
                onChartTypeChange={selectedChartType => {
                  this.setState({ selectedChartType, redraw: true });
                }}
              />
              <DownloadButton chartTitleText={chartTitleText} />
            </Grid>
          </MuiThemeProvider>
          <Grid item xs={12} md={6}>
            <ReactDataSheet
              data={getReactDataSheetDataFromData(this.state.data)}
              valueRenderer={cell => cell.value}
              onCellsChanged={changes => {
                const data = this.state.data.map(row => [...row]);
                changes.forEach(({ cell, row, col, value }) => {
                  data[row][col] = value;
                });
                this.setState({ data, redraw: false });
              }}
            />
          </Grid>
        </Grid>
        <div className="feedback-link-wrapper">
          <Link href="mailto:feedback@freechartonline.com" target="_blank">
            feedback@freechartonline.com
          </Link>
        </div>
      </div>
    );
  }
}

export default App;
